import React, { ReactNode } from "react";
import { useSelector } from "react-redux";
import { CUSTOMER } from "../../config/constants";
import { IAuthState } from "../../stateManagement/reducers/authReducer";
import { IRootState } from "../../stateManagement/store";
import CustomerDashboardLayout from "./CustomerDashboardLayout";

interface IHODashboardProps {
    children: ReactNode
}

const HODasboardLayout: React.FC<IHODashboardProps> = ({ children }) => {

    const { user } = useSelector<IRootState, IAuthState>(state => state.auth);

    switch (user?.role) {
        case CUSTOMER:
            return <CustomerDashboardLayout children={children} />
        default:
            return null;
    }

}

export default HODasboardLayout;