import { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { IAuthState } from "../../stateManagement/reducers/authReducer";
import { INavBarsState, selectNav } from "../../stateManagement/reducers/navBarsReducer";
import { IRootState } from "../../stateManagement/store";
import { Nav, NavsBarContainer, NavsList } from "./Style";
import { ReactComponent as MenuBtn } from '../../assets/svgs/menu.svg';
import { ReactComponent as CloseBtn } from '../../assets/svgs/close.svg';
import { ReactComponent as ExitIcon } from '../../assets/svgs/exit.svg';
import { useDispatch } from "react-redux";
import { useLocation, useNavigate, useParams } from "react-router-dom";

const NavsBar = () => {

    const dispatch = useDispatch<any>();
    const { user } = useSelector<IRootState, IAuthState>(state => state.auth);

    const navBars = useSelector<IRootState, INavBarsState>(state => state.navBars);

    const navigate = useNavigate();
    const location = useLocation();

    const [active, setActive] = useState(false);
    const toggleActive = () => {
        if (active) {
            setActive(false);
        } else {
            setActive(true);
        }
    }

    return (
        <>
            {
                (user && navBars[user?.role as keyof INavBarsState])

                &&

                <NavsBarContainer className={`${active ? 'active' : ''}`} >
                    <div
                        className="menuBtnFrame"
                        onClick={() => setActive(true)}
                    ><MenuBtn /></div>
                    <div className="mainNavsContainer">
                        <div
                            className="closeBtnFrame"
                            onClick={() => setActive(false)}
                        ><CloseBtn /></div>
                        <div className="navsBgFrame" ><img src='/images/navBg.png' alt="surface" /></div>
                        <NavsList className={`${active ? 'active' : ''}`} >
                            {navBars[user?.role as keyof INavBarsState].navs.map(nav => {
                                return (
                                    <Nav
                                        key={nav.navIndex}
                                        className={`${nav.selected ? 'selected' : ''}`}
                                        onClick={() => {
                                            dispatch(selectNav({ role: user.role, navIndex: nav.navIndex }));
                                            setActive(false);
                                            navigate(nav.bankRoute);
                                        }}
                                    >
                                        <span className="navIconFrame">{nav.icon}</span>
                                        <span className="navName" >{nav.name}</span>
                                    </Nav>
                                )
                            })}
                        </NavsList>
                        <Nav
                            className={`logoutNav`}
                            onClick={() => {
                                localStorage.clear();
                                window.open(location.pathname, '_self');
                            }}
                        >
                            <span className="navIconFrame"><ExitIcon /></span>
                            <span className="navName" >Logout</span>
                        </Nav>
                    </div>
                </NavsBarContainer>
            }
        </>
    );
}

export default NavsBar;