import React, { ReactNode, useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { useSelector } from 'react-redux';
import NavsBar from '../../components/NavsBar/NavsBar';
import { IAuthState } from '../../stateManagement/reducers/authReducer';
import { ICustomerState, tryFetchCustomer } from '../../stateManagement/reducers/customerReducer';
import { IRootState } from '../../stateManagement/store';
import { DashboardBody, DashboardLayoutContainer } from "./Style";
import { BANK_NAME } from '../../config/constants';

interface ICustomerDashboardLayoutProps {
    children: ReactNode
}

const CustomerDashboardLayout: React.FC<ICustomerDashboardLayoutProps> = ({ children }) => {

    const dispatch = useDispatch<any>();
    const customer = useSelector<IRootState, ICustomerState>(state => state.customer);
    const { user } = useSelector<IRootState, IAuthState>(state => state.auth);

    useEffect(() => {
        if (!customer.data) {
            dispatch(tryFetchCustomer({ authToken: user?.authToken ?? '' }));
        }
    }, [customer.data, user?.authToken]);

    useEffect(() => {
        if (customer.fetchErr) {
            alert(`Could not fetch customer-info: ${customer.fetchErr}`);
        }
    }, [customer.fetchErr]);

    return (
        <DashboardLayoutContainer>
            <div className='dashboardLogo' ><img src='/images/Logo.png' alt={BANK_NAME} /></div>
            <NavsBar />
            <DashboardBody>{children}</DashboardBody>
        </DashboardLayoutContainer>
    );
}

export default CustomerDashboardLayout;